$backgroundColor: rgba(12,10,29,0.96);
$screenColor: rgba(255,255,255,.1);

$xs: 575.98px;
$sm: 576px;
$md: 768px;
$lg: 992px;
$xl: 1200px;
$xxl: 1600px;

$headerHeight: 76px;
$headerHeightXs: 68px;

//$defaultFont: 'Roboto Mono', monospace;
$defaultFont: 'Inter', monospace;

$defaultFontColor: rgba(255,255,255,.88);
$defaultHeadingColor: rgba(255,255,255,0.96);
$primaryColor: #FF0079;
$secondaryColor: #73328d;
$tertiaryColor: #efc2d3;

$copyFont: 'Roboto', monospace;

$buttonBorderRadius: 0.125rem;
$buttonPrimaryBorder: 1px solid $primaryColor;
$buttonPrimaryBackground: none;
$buttonPrimaryColor: $primaryColor;

$buttonPrimaryBackgroundHover: $primaryColor;
$buttonPrimaryColorHover: $defaultFontColor;

$buttonTertiaryBorder: 1px solid $secondaryColor;
$buttonTertiaryBackground: $secondaryColor;
$buttonTertiaryColor: #fff;

$buttonTertiaryBackgroundHover: #fff;
$buttonTertiaryColorHover: $secondaryColor;

$sectionBorder: 1px solid rgb(44, 47, 54);
$tableBorder: 1px solid rgba(255,255,255,.5);

